.searchBar {
  display: flex;
  align-items: center;
  padding: 8px 16px;
  overflow: hidden;
}
.searchForm {
  border-radius: 4px;
  background-color: #666;
}
.inputBox {
  display: flex;
  align-items: center;
  position: relative;
  width: 343px;
  padding-left: 36px;
  padding-right: 36px;
  transition: width 0.2s;
}
input {
  padding: 4px 0;
  height: 24px;
  line-height: 24px;
  width: 100%;
  box-sizing: content-box;
  border: 0;
  font-size: 14px;
  color: 14px;
  background-color: transparent;
}
